body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.div1
{
  color: red;
  background-color: yellow;
}
.header {
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}
.header-1 {
  display: flex;
  /* flex-wrap: nowrap; */
  align-items: center;
}
.header-2 {
    line-height: 10px;
    text-align: end;  
}
.btnSubmit {
  margin-bottom: 20px;
  background-color: aqua;
  color: white;
  padding: 5px;
  border: none;
  border-radius: 5px;
}
.card {
  margin-top: 20px;
  width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: auto auto auto;
  justify-content: space-around;
}
.card-body {
   width: 33.33%; 
   margin: auto;
}
.card-body img {
  width: 100%;
}
.about {
  text-align: center;
}
.footer {
  width: 80%;
  margin: auto;
  display: flex;
  flex-wrap: nowrap;
  column-gap: 20px;
}
.footer-1 {
  background-color: white;
  width: 50%;
}
.footer-1 h4 {
  text-align: center;
}
.footer-2 {
  background-color: rgba(53, 47, 47,0.4);
  color: white;
  width: 50%;
}
.footer-2 h4 {
  text-align: center;
}

